const PlayerList = require('./player-list').PlayerList;
const LrcPlayer = require('./lrc-player').LrcPlayer;

let huMusicPlayer = document.querySelector("#hu-music-player")
let audioName = document.querySelector("#audio_name")
let lrcContainer = document.querySelector("#lrc-container")
let a = document.querySelector("img");


let playlistIndex = 0;



let lrcPlayer = new LrcPlayer(huMusicPlayer, lrcContainer);
lrcPlayer.loadLrc(PlayerList[playlistIndex].lyrics);
huMusicPlayer.src = PlayerList[playlistIndex].play_url;
audioName.innerHTML = PlayerList[playlistIndex].audio_name;
a.src = PlayerList[playlistIndex].img;
document.title = PlayerList[playlistIndex].audio_name + " - huang zhenjie";

